body {
  display: flex;
  margin: 0;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: lightslategrey;
}

.container {
  display: grid;
  width: 350px;
  background-color: lightslategrey;
  border: 4px solid #000000;
}

#title {
  background-color: rgb(224, 229, 233);
  font-weight: bold;
  line-height: 50px;
  text-align: center;
  border: 1px solid #000000;
}

#display {
  background-color: rgb(22, 60, 92);
  color: white;
  line-height: 50px;
  text-align: right;
  padding-right: 16px;
  border: 1px solid #000000;
}

#result {
  background-color: rgb(22, 60, 92);
  color: white;
  line-height: 50px;
  text-align: center;
  border: 1px solid #000000;
}

#button-area {
  display: grid;
  grid-template-rows: repeat(4, 50px);
  grid-template-columns: repeat(4, auto);
  border: 1px solid #000000;
}

.equals {
  display: grid;
  grid-template-rows: repeat(1, 50px);
}
